@import url("../node_modules/dseg/css/dseg.css");
html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Helvetica Neue", Helvetica, Roboto, Arial, sans-serif;
  font-weight: normal;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/*****************************************************************************/
.led-clock {
  background-color: black;
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
}

.led-clock__line {
  align-items: flex-end;
}

.led-clock__line--date {
  padding-top: 4vw;
  font-size: 5vw;
  padding-right: 2vw;
  flex: 0 1 50%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.led-clock__line--time {
  padding-top: 4rem;
  padding-bottom: 4rem;
  font-size: 20vw;
  flex: 0 0 auto;
  display: flex;
  flex-direction: row;
  align-self: center;
  justify-content: center;
}

.led-clock__line--blank {
  flex: 0 1 50%;
  padding-bottom: 4vw;
}

.led-clock__segment {
  display: inline-block;
  position: relative;
  font-family: 'DSEG7-Classic';
  font-weight: normal;
  font-style: italic;
  line-height: 1;
}

.led-clock__segment--weekday {
  font-family: 'DSEG14-Classic';
}

.led-clock__segment:not(.led-clock__segment--ampm) {
  position: relative;
}

.led-clock__segment:not(.led-clock__segment--ampm)::before {
  position: absolute;
  display: inline-block;
  top: 0;
  left: 0;
  content: '88';
}

.led-clock__segment:not(.led-clock__segment--ampm)::before, .led-clock__segment:not(.led-clock__segment--ampm)::after {
  opacity: 0.0625;
}

.led-clock__segment:not(.led-clock__segment--ampm).led-clock__segment--hour::before {
  content: '88';
}

.led-clock__12-hour .led-clock__segment:not(.led-clock__segment--ampm).led-clock__segment--hour::before {
  content: '18';
}

.led-clock__segment:not(.led-clock__segment--ampm).led-clock__segment--colon::before {
  content: ':';
}

.led-clock__segment:not(.led-clock__segment--ampm).led-clock__segment--second {
  font-size: 75%;
}

.led-clock__segment:not(.led-clock__segment--ampm).led-clock__segment--weekday {
  font-size: 75%;
}

.led-clock__segment--ampm {
  font-family: 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
  font-style: normal;
  background-color: rgba(0, 0, 255, 0.05);
  position: relative;
}

.led-clock.led-clock__12-hour .led-clock__segment--ampm {
  width: 0;
}

.led-clock.led-clock-24-hour .led-clock__segment--ampm {
  display: none;
}

.led-clock__segment--ampm::before {
  position: absolute;
  top: 0;
  bottom: auto;
  left: 0;
  font-size: 25%;
  line-height: 0.75;
  content: 'AM';
}

.led-clock__segment--ampm::after {
  position: absolute;
  top: auto;
  bottom: 0;
  left: 0;
  font-size: 25%;
  line-height: 0.75;
  content: 'PM';
}

.led-clock__segment--ampm::before, .led-clock__segment--ampm::after {
  opacity: 0.0625;
}

.led-clock__segment--ampm.am::before {
  opacity: 1;
}

.led-clock__segment--ampm.pm::after {
  opacity: 1;
}

body.led-clock__page {
  min-height: 100vh;
  overflow: hidden;
  background-color: black;
}

.led-clock__window {
  font-family: 'DSEG7-Classic';
  font-weight: 400;
  font-style: italic;
}

.led-clock__line {
  color: yellow;
}

.css-reset {
  all: unset;
}

.bottom-left-buttons {
  position: fixed;
  bottom: 0;
  left: 0;
}

.bottom-right-buttons {
  position: fixed;
  bottom: 0;
  right: 0;
}

.bottom-left-buttons,
.bottom-right-buttons {
  font-size: 200%;
  padding: 0.5em;
  color: rgba(255, 255, 255, 0.25);
}

.bottom-left-buttons a,
.bottom-right-buttons a {
  color: inherit;
  text-decoration-style: dotted;
}

/**
 * Push Buttons and Switches
 */
.push-button {
  display: inline-block;
  vertical-align: middle;
  border: 2px solid black;
  box-sizing: padding-box;
  height: 1.4em;
  width: 2.8em;
  border-radius: 1em;
  position: relative;
  top: -0.1em;
  background-color: #ccc;
  background-image: linear-gradient(to bottom, #eee, #999);
}

.push-button::after {
  content: '';
  position: absolute;
  border-radius: calc(1em - 3px);
  top: 1px;
  bottom: 1px;
  left: 1px;
  right: 1px;
  background-image: linear-gradient(to bottom, #999, #fff);
  content: '';
}

.toggle-switch {
  box-sizing: content-box;
}

.toggle-switch::before {
  box-sizing: content-box;
}

[type="checkbox"].toggle-switch {
  width: 0;
  opacity: 0;
  display: none;
}

[type="checkbox"].toggle-switch + :not([type="checkbox"]).toggle-switch {
  display: inline-block;
  vertical-align: middle;
  border: 2px solid black;
  height: 1.4em;
  width: 2.8em;
  background-color: black;
  border-radius: 1em;
  position: relative;
  top: -0.1em;
  overflow: hidden;
}

[type="checkbox"].toggle-switch + :not([type="checkbox"]).toggle-switch::before {
  background-color: #cc6600;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 50%;
  width: 100%;
  height: 1.4em;
  border-radius: calc(1em - 2px);
  content: '';
  transition: right 125ms;
}

[type="checkbox"]:checked.toggle-switch + :not([type="checkbox"]).toggle-switch::before {
  right: 0;
}

[type="checkbox"].toggle-switch + :not([type="checkbox"]).toggle-switch > .inner {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 50%;
  height: 1.4em;
  width: 1.4em;
  border-radius: calc(1em - 2px);
  background-color: #ccc;
  background-image: linear-gradient(to bottom, #eee, #999);
  content: '';
  transition: right 125ms;
}

[type="checkbox"].toggle-switch + :not([type="checkbox"]).toggle-switch > .inner::after {
  content: '';
  position: absolute;
  border-radius: calc(1em - 2px - 1px);
  top: 1px;
  bottom: 1px;
  left: 1px;
  right: 1px;
  background-image: linear-gradient(to bottom, #999, #fff);
}

[type="checkbox"]:checked.toggle-switch + :not([type="checkbox"]).toggle-switch > .inner {
  right: 0;
}

.bottom-panel a {
  color: inherit;
  text-decoration-style: dotted;
}
